import React from 'react';
import { graphql, navigate } from 'gatsby';
import { getSrc } from 'gatsby-plugin-image';
import Box from '@mui/material/Box';
import Chip from '@mui/material/Chip';
import Divider from '@mui/material/Divider';
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import CreateIcon from '@mui/icons-material/Create';
import LocalOfferIcon from '@mui/icons-material/LocalOffer';
import Layout from '../components/layout';
import Bio from '../components/bio';
import Link from '../components/link';

const BlogPostTemplate = ({ data, location }) => {
  const {
    previous,
    next,
    markdownRemark: post,
    site: {
      siteMetadata: { siteUrl }
    }
  } = data;

  const TwitterShareButton = () => {
    let title = post.frontmatter.title;
    const maxLength = 100;

    if (title.length > maxLength) {
      title = post.frontmatter.title.substr(0, maxLength) + '...';
    }

    return (
      <Box my={2}>
        <a
          href='https://twitter.com/share?ref_src=twsrc%5Etfw'
          className='twitter-share-button'
          data-size='large'
          data-text={title}
          data-url={location.origin + location.pathname}
          data-via='sheep_null'
          data-lang='ja'
          data-show-count='false'
        >
          Tweet
        </a>
      </Box>
    );
  };

  const thumbnailUrl = post.frontmatter.thumbnail
    ? siteUrl + getSrc(post.frontmatter.thumbnail)
    : `https://images-na.ssl-images-amazon.com/images/P/${post.frontmatter.asins[0]}.09.LZZZZZZZ`;

  const Afs = () => (
    <Box>
      <Box
        sx={{
          display: 'inline-flex',
          width: '100%',
          overflow: 'scroll'
        }}
      >
        {post.frontmatter.asins?.map((asin, i) => {
          return (
            <Box key={i} mr={2}>
              <a
                href={`https://www.amazon.co.jp/dp/${asin}/ref=nosim?tag=sheepnull0b-22`}
                target='_blank'
                rel='noreferrer'
              >
                <img
                  src={`https://images-na.ssl-images-amazon.com/images/P/${asin}.09.LZZZZZZZ`}
                  alt={asin}
                  style={{
                    width: 120,
                    height: 160,
                    objectFit: 'contain'
                  }}
                />
              </a>
            </Box>
          );
        })}
      </Box>
      <Typography variant='body2' color='gray'>
        Amazon リンク
      </Typography>
    </Box>
  );

  return (
    <Layout
      location={location}
      title={post.frontmatter.title}
      description={post.frontmatter.description || post.excerpt}
      thumbnail={thumbnailUrl}
    >
      <Box component='article' itemScope itemType='http://schema.org/Article'>
        <Box component='header' mb={2}>
          <Typography itemProp='headline' variant='h1'>
            {post.frontmatter.title}
          </Typography>
          <Box display='flex' alignItems='center' my={1}>
            <CreateIcon
              sx={{
                fontSize: 16
              }}
            />
            <Typography variant='body2'>{post.frontmatter.date}</Typography>
          </Box>
          <Stack
            direction='row'
            spacing={1}
            my={1}
            sx={{
              overflow: 'scroll'
            }}
          >
            {post.frontmatter?.tags?.map(label => (
              <Chip
                key={label}
                label={label}
                onClick={() => navigate(`/tags/${label}/`)}
                icon={
                  <LocalOfferIcon
                    sx={{
                      fontSize: 16
                    }}
                  />
                }
              />
            ))}
          </Stack>
        </Box>
        <TwitterShareButton />
        <Afs />
        <Box
          component='section'
          dangerouslySetInnerHTML={{ __html: post.html }}
          itemProp='articleBody'
        />
        <TwitterShareButton />
        <Afs />
        <Divider
          sx={{
            my: 5
          }}
        />
        <Box component='footer'>
          <Bio />
        </Box>
      </Box>
      <Box component='nav' className='blog-post-nav'>
        <Grid container spacing={2} my={2} fontSize={12}>
          <Grid item md={6}>
            {previous && (
              <Link
                href={`/articles${previous.fields.slug}`}
                rel='prev'
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center'
                }}
              >
                <ArrowBackIosIcon /> {previous.frontmatter.title}
              </Link>
            )}
          </Grid>
          <Grid item md={6}>
            {next && (
              <Link
                href={`/articles${next.fields.slug}`}
                rel='next'
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'flex-end'
                }}
              >
                {next.frontmatter.title} <ArrowForwardIosIcon />
              </Link>
            )}
          </Grid>
        </Grid>
      </Box>
    </Layout>
  );
};

export default BlogPostTemplate;

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
        siteUrl
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(truncate: true, pruneLength: 140)
      html
      frontmatter {
        title
        date(formatString: "YYYY年MM月DD日")
        description
        tags
        thumbnail {
          childImageSharp {
            gatsbyImageData(width: 200)
          }
        }
        afs {
          asins
          linkId
        }
        asins
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`;
